import { Component, OnInit } from '@angular/core';
import { NavController, ActionSheetController, LoadingController, AlertController, ToastController } from '@ionic/angular';
import { AppMin } from "../core"




interface objList {
  state: string,
  date: string,
  money: string
}

@Component({
  selector: 'app-mywallet-record',
  templateUrl: './mywallet-record.page.html',
  styleUrls: ['./mywallet-record.page.scss'],
})
export class MywalletRecordPage implements OnInit {
  header_class;
  loader;

  listobj = [];
  ret_data_leth: number = 0;
  constructor(public navCtrl: NavController, public appmin: AppMin, public loadingCtrl: LoadingController) {

    if (this.appmin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      console.log("判断为安卓系统 设置状态栏样式")
    }




  }


  send(value, isshow?: boolean) {


    if (isshow) {
      this.loader = this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000

      });
      this.loader.present();
    }
    this.appmin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


  }
  dataHandle(meg: any) {

    if (meg['type'] == 'addMyWalletRecordPage_ret') {
      //
      console.log('addMyWalletRecordPage_ret=' + JSON.stringify(meg))

      this.listobj = meg['rows'];

      this.ret_data_leth = this.listobj.length; //设置返回的数据行数 用来判断是否显示没有数据的提示
      console.log("ret_data_leth=" + this.ret_data_leth);
    }

  }
  addMyWalletRecordPage() {
    //加载数据
    let obj = {
      type: 'addMyWalletRecordPage',
      userId: this.appmin.getUserid()
    }
    this.send(JSON.stringify(obj));


  }


  ngOnInit() {
    this.addMyWalletRecordPage();
  }

}
